import React, { Component } from "react";
import axios from "axios";
import maintance from "../Images/maintance.svg";

const server = process.env.REACT_APP_BACKEND_URL;
class SaldoAwal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      SaldoAwal: [],
      ready: false,
      response: "",
      display: "none",
      alert:
        "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
      kode_provinsi: "",
      nama_provinsi: "",
    };
    this.kode_Change = this.kode_Change.bind(this);
    this.nama_Change = this.nama_Change.bind(this);
  }

  componentDidMount() {
    axios
      .get(server)
      .then((res) =>
        this.setState({
          SaldoAwal: res.data,
        })
      )
      .catch((err) =>
        this.setState({
          alert:
            "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
        })
      );
  }

  componentDidUpdate() {
    console.log(this.state);
  }
  kode_Change(e) {
    this.setState({
      kode_provinsi: e.target.value,
    });
  }
  nama_Change(e) {
    this.setState({
      nama_provinsi: e.target.value,
    });
  }
  submit_click(e) {
    console.log(this.state);
  }

  render() {
    return (
      <div className="text-center">
        <img src={maintance} alt="MAINTANCE" width={450} />
        <h1>Under Maintance</h1>
      </div>
    );
  }
}

export default SaldoAwal;

